@import url(https://fonts.googleapis.com/css2?family=Tinos:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Tinos', serif;
}
html,
body{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.container h1{
  text-align: center;
  font-size: 30px;
}
.loading-bar {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  -webkit-animation: loading-bar2 3s 0s linear both;
          animation: loading-bar2 3s 0s linear both;
}
.loading-bar span {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  -webkit-animation: loading-bar 1s 2s linear infinite;
          animation: loading-bar 1s 2s linear infinite;
  margin-right: 12px;
}
.loading-bar span:nth-child(1) {
  background-color: crimson;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.loading-bar span:nth-child(2) {
  background-color: dodgerblue;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}
.loading-bar span:nth-child(3) {
  background-color: yellowgreen;
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}
.loading-bar h2 {
  margin: 0;
  font-size: 30px;
}
.footer {
  border-top: 1px solid #ddd;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px 0;
}
.footer__icon__container {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 10px;
}
.footer__icon {
  cursor: pointer;
  margin-right: 10px;
  transition: all 0.2s ease-in-out;
}
.footer__icon:hover{
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}
.footer__icon:last-child{
  margin-right: 0;
}
.footer__icon img{
  width: 45px;
  border-radius: 50%;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.footer__text {
  font-size: 16px;
  color: #444444;
  font-weight: bold;
}

@-webkit-keyframes loading-bar {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0.2;
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
  }
}

@keyframes loading-bar {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0.2;
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
  }
}
@-webkit-keyframes loading-bar2 {
  0% {
    margin-left: -30px;
    opacity: 0;
  }
  30% {
    margin-left: 0px;
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes loading-bar2 {
  0% {
    margin-left: -30px;
    opacity: 0;
  }
  30% {
    margin-left: 0px;
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.movies {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 20px;
  padding-top: 40px;
  padding-bottom: 10px;
  width: 100%;
}
.movies .movie {
  width: 23.5%;
  height: 260px;
  background-color: white;
  margin-bottom: 50px;
  display: -webkit-flex;
  display: flex;
  font-weight: 300;
  padding: 20px;
  border-radius: 10px;
  color: #adaeb9;
  box-shadow: 0 13px 27px 0 rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  cursor: pointer;
  transition: all 0.2s ease;
}
.movies .movie:hover{
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}
.movie img {
  position: relative;
  max-width: 150px;
  width: 100%;
  margin-right: 20px;
  border-radius: 10px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movie__data{
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 13px;
}
.movie .movie__title {
  margin: 0px;
  font-size: 20px;
  color: black;
  font-weight: bold;
  display: block;
}
.movie .movie__year{
  margin: 0px;
  font-size: 20px;
  color: black;
  font-weight: bold;
  display: block;
}
.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  margin-top: 15px;
}
.movie .movie__genres li{
  color: #adaeb9;
  font-weight: 300; 
  font-size: 15px;
  font-weight: bold;
  margin-right: 10px;
  color: crimson;
}
.movie .movie__rating{
  margin-top: 5px;
  color:gray;
}
.movie .movie__rating i{
  margin-right: 5px;
  color: gold;
}
.movie .movie__summary{
  color: gray;
}
